
*{
  margin:0 ;
  padding:0;
  font-size: 14px;
}
.pop-mask{
  position:fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background:rgba(0,0,0,0.3);
  z-index: 1;
}

body {
  background: #0d0d0d;
}
.line-chart {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 0.05rem;
  background: #262626;
  border-bottom: 1px solid #262626;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.line-chart ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin: 0 4rem;
}
.line-chart ul li {
      text-align: center;
      list-style: none;
}
.line-chart ul li .box {
        position: absolute;
        bottom: 0;
        width: 3.5rem;
        height: 0;
        background: #f22323;
        text-align: center;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        -webkit-transition: height 1s;
        transition: height 1s;
}
.line-chart ul li .box .num {
          position: absolute;
          top: -1.4rem;
          left: 0;
          right: 0;
          display: inline-block;
          color: #f22323;
          font-weight: 500;
          font-size: 1.2rem;
          line-height: 1;
}
.line-chart ul li.position-top .box {
        top: 0;
        background: #00a000;
}
.line-chart ul li.position-top .box .num {
          color: #00a000;
}
